@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  box-sizing: border-box;
}

ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

ul li {
  list-style-type: none;
  display: block;
}

body,
html {
  background-color: #071013;
}

:root {
  --global-color-black: rgb(7, 16, 19);
  --global-color-grey1: rgb(84, 91, 94);
  --global-color-grey2: rgb(125, 132, 134);
  --global-color-grey3: rgb(176, 178, 180);
  --global-color-grey4: rgb(226, 226, 226);
  --global-color-grey5: rgb(240, 243, 244);
  --global-color-red: rgb(248, 51, 60);
  --global-color-red-hover: rgb(245, 78, 86);
  --global-color-white: white;
  --global-side-padding: 20rem;
  --global-top-bottom-padding: 10rem;
}

p {
  line-height: 1.5rem;
  margin: 0;
}

h1 {
  font-size: 64px;
}

h2 {
  font-size: 56px;
}

h3 {
  font-size: 48px;
}

h4 {
  font-size: 40px;
}

h5 {
  font-size: 32px;
  line-height: 40px;
}

h6 {
  font-size: 20px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.font-large {
  font-size: 20px;
  line-height: 25px;
}

.font-regular {
  font-size: 16px;
}

.font-small {
  font-size: 14px;
}

.font-micro {
  font-size: 12px;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Kumbh Sans", sans-serif;
  height: 100%;
  width: 100%;
}

a {
  text-decoration: none;
}

input {
  font-family: "Kumbh Sans", sans-serif;
}

.section {
  width: 100%;
  padding: 10rem 20rem;
  padding: var(--global-top-bottom-padding) var(--global-side-padding);
  color: white;
  color: var(--global-color-white);
}

.section .section-title {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  align-items: flex-start;
  width: 40%;
  text-align: start;
}

.section .section-title span {
  text-decoration: line-through;
}

.section .section-title h4 {
  margin-bottom: 1rem;
  text-align: start;
  font-weight: 800;
  font-size: 26px;
}

.section .section-title h5 {
  font-weight: 800;
  font-size: 32px;
  white-space: pre-line;
  margin-bottom: 30px;
}

.section .section-title h6 {
  margin-bottom: 1rem;
}

.section .section-title p {
  color: rgb(125, 132, 134);
  color: var(--global-color-grey2);
}

.section .section-title .icon-numbering {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}

.section .section-title .icon-numbering .line1,
.section .section-title .icon-numbering .line2 {
  height: 1px;
  width: 3rem;
  background: rgb(248, 51, 60);
  background: var(--global-color-red);
  margin-right: 0.5rem;
  margin-bottom: 2px;
}

.section .section-title .icon-numbering .line2 {
  margin-right: 0rem;
  margin-left: 0.5rem;
}

.section .section-title .icon-numbering .number {
  color: rgb(248, 51, 60);
  color: var(--global-color-red);
  font-size: 0.75rem;
}

.section .section-title-center {
  align-items: center;
  width: auto;
}

.section .section-title-center .icon-numbering {
  margin-bottom: 1rem;
}

.section .section-title-center h4 {
  text-align: center;
}

.section .section-title-side .icon-numbering {
  position: absolute;
  transform: translate(25%, 0);
  right: 100%;
  top: -2rem;
  align-items: flex-end;
}

.section .section-title-side .line2 {
  display: none;
}

.email-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: rgb(7, 16, 19);
  color: var(--global-color-black);
}

.email-container .input-form {
  width: 100%;
  height: 5rem;
}

.email-container .input-form .error-message {
  margin-top: 0.5rem;
}

.email-container .email-sent {
  color: green;
}

.email-container .input {
  background: transparent;
  height: 3rem;
  width: 20rem;
  border-radius: 2rem;
  padding-left: 1rem;
  border: 1px solid rgb(84, 91, 94);
  border: 1px solid var(--global-color-grey1);
  margin-right: 1rem;
  outline: none;
}

.email-container .error-message {
  color: rgb(248, 51, 60);
  color: var(--global-color-red);
}

.btn {
  cursor: pointer;
  width: auto;
  display: inline-block;
  transition: 0.3s all ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.btn-round {
  background: rgb(248, 51, 60);
  background: var(--global-color-red);
  padding: 1rem 2rem;
  border-radius: 2rem;
  font-weight: 700;
  font-family: "Kumbh Sans", sans-serif;
  color: white;
  color: var(--global-color-white);
  outline: none;
  border: none;
}

.btn-round:hover {
  background: rgb(245, 78, 86);
  background: var(--global-color-red-hover);
}

.btn-hollow {
  background: transparent;
  border: 1px solid rgb(125, 132, 134);
  border: 1px solid var(--global-color-grey2);
  color: rgb(125, 132, 134);
  color: var(--global-color-grey2);
}

.btn-hollow:hover {
  color: rgb(248, 51, 60);
  color: var(--global-color-red);
  border-color: rgb(245, 78, 86);
  border-color: var(--global-color-red-hover);
  background: transparent;
}

.website-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  background: rgb(7, 16, 19);
  background: var(--global-color-black);
  width: 100%;
}

.main-sections {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.header {
  position: fixed;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  width: 100%;
  top: 0;
  z-index: 3;
  transform: translate3d(0, 0, 0);
}

.header .sidebar {
  position: absolute;
  top: 5rem;
  display: none;
  width: 100%;
  height: calc(100vh - 5rem);
  background: rgb(7, 16, 19);
  background: var(--global-color-black);
}

.navbar {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
