  height: 5rem;
  padding: 0 3rem;
}

.navbar .burger-menu {
  display: none;
}

.navbar .container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.navbar .container-left {
  height: 100%;
}

