}

.navbar .container-left .btn:hover {
  color: rgb(248, 51, 60);
  color: var(--global-color-red);
  border-bottom: 3px solid rgb(248, 51, 60);
  border-bottom: 3px solid var(--global-color-red);
}

.navbar .container-contact .btn {
